export default {
	data() {
		return {
			type: "全部",
			dzfType: '', //判断待支付是否为空
			pageIndex: 1,
			orderList: [], //订单列表
			token: ''

		}
	},
	onLoad: function(options) {
		this.token = uni.getStorageSync('token')
		this.type = options.type;

	},
	onShow: function() {
		this.getOrderList()
	},
	onReachBottom: function() {
		// this.pageIndex ++ ;
		// this.getOrderList()
	},
	methods: {
		//获取订单列表
		getOrderList: function() {
			var data = {
				pageIndex: this.pageIndex,
				token: this.token,
				pagec: 2,
				orderState: this.type
			}
			this.$urlRequest('getOrderList.do', data).then(res => {
				if (res.data.length == 0) {
					this.dzfType = true
				}
				for (var item of res.data) {
					for (var childItem of item.goodsList) {
						for (var key in childItem) {
							if (key == 'goodsImg') {
								childItem[key] = JSON.parse(childItem[key])
							}
						}
					}
					if (item.state == '待支付') {
						item['type'] = '去支付'
					} else if (item.state == '待发货') {
						item['type'] = '提醒发货'
					} else if (item.state == '待收货') {
						item['type'] = '确认收货'
					} else if (item.state == '待评价') {
						item['type'] = '去评价'
					} else if (item.state == '已完成') {
						item['type'] = '再来一单'
					}
				}

				if (this.pageIndex == 1) {
					this.orderList = res.data
				} else {
					if (res.data.length == 0) {
						this.$myToast('暂无更多订单')
						return;
					}
					this.orderList.push(res.data)
				}

			})
		},
		//切换状态
		changeType: function(type) {
			this.dzfType = ''
			this.pageIndex = 1;
			this.type = type
			this.getOrderList()
		},

		gotoClick: function(type, orderNo,id) {
			if (type == '去支付') {
				this.gotoPay(orderNo)
			}else if(type == '确认收货'){
				this.confirmProd(type,id)
			}else if(type == '提醒发货'){
				this.confirmProd(type,id)
			}
		},
		
		//确定收货 //提醒发货
		confirmProd:function(type,id){
			if(type == '确认收货'){
				var title = '收货成功'
				var url = 'confirmOrder.do'
			}else{
				var title = '已通知商家尽快发货'
				var url = ''
			}
			var data = {
				token:this.token,
				id:id
			}
			this.$urlRequest(url,data).then(res=>{
				if(res.code == '1'){
					this.$myToast(title)
					this.getOrderList()
				}else{
					this.$myToast(res.msg)
				}
			})
		},
		
		//支付
		gotoPay: function(orderNo) {
			uni.showLoading({
				title: '正在支付中'
			});
			uni.login({
				success: res => {
					//登录注册接口
					if (res.code) {
						// 调用服务端登录接口，发送 res.code 到服务器端换取 openId, sessionKey, unionId并存入数据库中
						this.$urlRequest('getOpenId2.do', {
							code: res.code
						}).then(res => {
							this.hlb(orderNo, res.openid)
						})
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			})
		},

		//调用支付
		hlb: function(orderNo, openId) {
			var that = this
			this.$urlRequest('unifiedorder.do', {
				billNo: orderNo,
				openId: openId
			}).then(res => {
				uni.hideLoading()
				uni.requestPayment({
					provider: 'wxpay',
					'timeStamp': res.timeStamp,
					'nonceStr': res.nonceStr,
					'package': res.package,
					'signType': res.signType,
					'paySign': res.paySign,
					'success': function(ret) {
						if (ret.errMsg == "requestPayment:ok") {
							that.$myToast('支付成功,请到全部中查看')
							that.getOrderList()
							that.sendModel(openId,res.package,orderNo)	
						}

					},
					'fail': function(res) {
						uni.hideLoading()
						that.$myToast(res)
					},
					'complete': function(res) {}
				})
			})
		},

		//前往订单详情
		gotoOrderD: function(id) {
			uni.navigateTo({
				url: "/pages/orderDetail/orderDetail?id=" + id
			})
		},
		//删除订单
		delOrder: function(id) {
			uni.showModal({
				content: '确定删除该订单?',
				success: (res) => {
					if (res.confirm) {
						this.$urlRequest('forgetOrderById.do', {
							id: id,
							token: this.token
						}).then(res => {
							if (res.code == '1') {
								this.$myToast('删除订单成功')
								this.getOrderList()

							} else {
								this.$myToast(res.msg)
							}
						})
					}
				}
			});

		},
		//发送模板
		sendModel:function(openId,Package,billNo){
			console.log(111)
			var data = {
				openid:openId,
				formid:Package,
				billNo:billNo
			}
			uni.requestSubscribeMessage({
				tmplIds: ['N7iYUpeuBKbr5TNsYt2HU1_OfH1IgFO6HMgYJMCHNVo'],
				success: (res) => {
					// this.$myToast('走到了成功函数')
				},
				fail:(res)=>{
					// this.$myToast('走到了失败函数')
				},
				complete:(res)=>{
					this.$urlRequest('sendTemplateMessage.do',data).then(ret=>{
						// this.$myToast('发送成功')
					})
				}
			})
		}

	}
}
